/* Global styles */
body {
  font-family: Roboto, Helvetica, sans-serif;
  text-align: center;
  margin: 0;
  padding: 0;
  background-color: #f8f9fa;
}

h1 {
  font-size: 2rem;
  margin-top: 20px;
  margin-bottom: 10px;
}

button {
  font-size: 18px;
  padding: 10px 20px;
  cursor: pointer;
}

/* Game grid styles */
#game-grid {
  display: grid;
  grid-template-columns: repeat(3, 100px);
  grid-template-rows: repeat(3, 100px);
  gap: 10px;
  justify-content: center;
  margin: 20px auto;
}

#game-grid > button {
  width: 100px;
  height: 100px;
  font-size: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #000;
  cursor: pointer;
  background-color: #fff;
}

/* Player-specific styles */
.X {
  color: red;
}

.O {
  color: blue;
}

button:hover {
  background-color: #f0f0f0;
}
